/* RomiStyle.css */
/* Styles généraux */
.romi-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

/* En-tête */
.romi-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-bottom: 20px;
}

.romi-header-left {
    display: flex;
    align-items: center;
}

.romi-character {
    width: 50px;
    height: 50px;
    margin-right: 10px;
}

.romi-welcome {
    font-size: 24px;
    margin: 0;
}

/* Zone principale */
.romi-main {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}

/* Panneau des thèmes */
.romi-themes-panel {
    width: 100%;
    margin-bottom: 20px;
}

.theme-buttons {
    display: flex;
    justify-content: center;
    gap: 20px;
}

/* Grille de boutons */
.romi-buttons-grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(3, 1fr);
    gap: 10px;
    width: 100%;
    margin-bottom: 20px;
}

.romi-button {
    font-family: Arial, sans-serif;
    color: black;
    border: 2px solid black;
    padding: 10px;
    margin: 0;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    background-color: #f0f0f0;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.romi-button:hover {
    background-color: #ddd;
}

.romi-button.empty-slot {
    background-color: #e0e0e0;
    cursor: default;
}

.romi-button.found {
    opacity: 0.5;
    cursor: not-allowed;
}

/* Styles pour la vidéo */
.romi-preview {
    width: 100%;
    max-width: 800px;
    margin: 20px 0;
}

.video-container {
    position: relative;
    width: 100%;
    padding-top: 56.25%;
    background: black;
    border-radius: 5px;
    overflow: hidden;
}

.preview-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

/* Boutons d'action */
.romi-action-buttons {
    display: flex;
    justify-content: center;
    gap: 20px;
    width: 100%;
    margin-top: 20px;
}

/* Styles des boutons de thème */
.romi-theme-button {
    font-family: Arial, sans-serif;
    color: black;
    border: 2px solid black;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    background-color: #f0f0f0;
    min-width: 150px;
}

.romi-theme-button:hover {
    background-color: #ddd;
}

.romi-theme-button.active {
    background-color: #aaa;
}

/* Styles Bluetooth */
.romi-button.bluetooth {
    background-color: #f0f0f0;
}

.romi-button.bluetooth.connected {
    background-color: #4CAF50;
    color: white;
}

.loading {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    background: rgba(0, 0, 0, 0.7);
    padding: 10px 20px;
    border-radius: 5px;
}

/* Header right section */
.romi-header-right {
    display: flex;
    align-items: center;
    gap: 10px;
}